.App {
  text-align: center;
}

.bgContent {
  /* background: url('./img/bg.jpg'); */
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.promiseItem {
  position: relative;
  background-color: #F3F3F3;
  margin: 30px 10%;
  /* width: 80%; */
  height: 165px;
  /* 阴影 */
  -webkit-box-shadow: 4px 2px 10px #a69d9d;
          box-shadow: 4px 2px 10px #a69d9d;
  /* border-top-left-radius: 50px;
  border-bottom-right-radius: 50px; */
}

.firstLevel , .secondLevel , .thirdLevel , .fourthLevel {
  position: relative;
  height: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 20px;
}

.firstLevel span {
  line-height: 40px;
  font-size: 16px;
}

.secondLevel span {
  line-height: 40px;
  width: 50%;
  font-size: 16px;
}

.thirdLevel span {
  width: 100%;
  text-align: right;
  font-size: 16px;
}

.fourthLevel span {
  width: 100%;
  text-align: right;
  font-size: 16px;
}

.submit {
  margin:20px;
  font-size: 16px;
  width: 70px;
  height: 30px;
}



.addButtonStyle {
  position: fixed;
  bottom: 150px;
  right: 7%;
  background-color: #FFBA08;
  color:#FFFFFF;
  width: 80px;
  height: 40px;
  border-radius: 5px;
  border:0;
}

.footerStyle {
  /* position: absolute; */
  bottom: 0;
  background-color: #F0F0F0;
  text-align: center;
}

.footerStyle span {
  display: inline-block;
  width: 100%;
  font-size: 14px;
  color: #9B9B9B;
}

/* 阴影框 */
.addContent {
  position: absolute;
  width: 100%;
  height: 1100px;
  background-color: rgba(0,0,0,0.4);
  z-index: 1000;
}

.frameBorder {
  position: absolute;
  left: 50%;
  top: 564px;
  width: 700px;
  height: 388px;
  margin: -350px 0 0 -376px;
  background-color: #F2F2F2;
  z-index: 1;
}

.nameInfo {
  margin: 30px auto;
}

.backBtn {
  width: 12px;
  height: 12px;
  margin-left: 600px;
  margin-top: 30px;
}



